import React from 'react'
import { Link } from 'react-router-dom'

class Headers extends React.Component{
    // constructor(){
    //     super()
    // }

    render(){
        const navstyle = {
            color: 'white'
        }
        return(
            <div>
            <div className="container-fluid bg-primary p-5"><h1 className="display-4 text-white">PREMIER LEAGUE</h1> 
            <p>By Bipin TUladhar</p></div>
            <div id = 'navs'>
            <nav>
                <ul id='listitem'>
                    <Link to='/' style={navstyle}>
                        <li>Home</li>
                    </Link>
                    <Link to='/Fixtures' style={navstyle}>
                        <li>Fixtures</li>
                    </Link>
                    <Link to='/Table' style={navstyle}>
                        <li>Table</li>
                    </Link>
                    <Link to='/Teams' style={navstyle}>
                        <li>Teams</li>
                    </Link>
                    <Link to='/Players' style={navstyle}>
                        <li>Players</li>
                    </Link>
                    <Link to='/Stats' style={navstyle}>
                        <li>Stats</li>
                    </Link>
                    
                </ul>
            </nav>
        </div>
            </div>
        )
    }
}

export default Headers