import React from 'react';

class Fixtures extends React.Component{
    constructor(){
        super()
        this.state = {
           fixtures : []
        }
        this.getFixtures = this.getFixtures.bind(this)
    }

    getFixtures(event){
        const num = event.target.value
        const api_key = '8ae6dd9f44804cdfa79b7cd38f77d800'
        const option = {
            method: 'GET', 
            headers: { 'X-Auth-Token': `${api_key}`  }
        }
        fetch(`http://api.football-data.org/v2/competitions/PL/matches/?matchday=${num}`, option)
        .then(res => res.json())
        .then(data =>{
            // console.log(data.matches)
            this.setState({ fixtures: data.matches})
    })
    }

    componentDidMount(){
        // console.log('bipin')
        const api_key = '8ae6dd9f44804cdfa79b7cd38f77d800'
        const option = {
            method: 'GET', 
            headers: { 'X-Auth-Token': `${api_key}`  }
        }
        fetch(`http://api.football-data.org/v2/competitions/PL/matches/?status=SCHEDULED`, option)
        .then(res => res.json())
        .then(data =>{
            // console.log(data)
            this.setState({ fixtures: data.matches})
    })
}

    render(){
        const newArr = this.state.fixtures.map((item) => {
            return(
            <li key={item.id} className='list-group-item'>I{item.utcDate}   {item.homeTeam.name} vs {item.awayTeam.name}</li>
            )

        })
        const Arr = []
        for(var i = 0; i< 38 ; i++){
        Arr[i] = <option key={`${i}`}value={`${i+1}`}>{`Gameweek ${i+1}`}</option>
        }

        const groupedArray = this.state.fixtures.reduce(function (r, a) {
            r[a.utcDate.substr(0, a.utcDate.indexOf('T'))] = r[a.utcDate.substr(0, a.utcDate.indexOf('T'))] || [];
            r[a.utcDate.substr(0, a.utcDate.indexOf('T'))].push(a);
            return r;
        }, Object.create(null));
        // console.log(groupedArray)
        
        const keysArray = Object.keys(groupedArray)
    
        const Arr1 = keysArray.map((item, index)=>{
        return (<ul key={index} className='list-group' style={{marginTop:"20px", textAlign:'center', fontWeight:'bolder'}}>{keysArray[index]} {groupedArray[item].map((items)=>{
            if(items.status === 'SCHEDULED'){
                return (
                <li key={items.id} className='list-group-item'>{items.homeTeam.name} : {items.awayTeam.name} </li>
            )}
            else{
                return(
                    <li key={items.id} className='list-group-item'>{items.homeTeam.name} {items.score.fullTime.homeTeam}: {items.score.fullTime.awayTeam} {items.awayTeam.name} </li>
                )
            }})} </ul>)
        })
        // console.log(Arr1)
        // console.log(groupedArray[keysArray[0]])

        //  console.log(Object.keys(result))
        // console.log(Arr)
       
        return(

            <div>
                 <form>
                <select name="gameweek"  className="custom-select" id="abc" onChange={this.getFixtures}>
                <option>Select the Gameweek for Results</option>
                   {Arr}
                </select>
             </form>          
                <div>
                    {/* <ul className='list-group'>
                    {newArr}
                    </ul> */}
                    {Arr1}
                </div>
            </div>
        )
    }
}

export default Fixtures