import React from 'react';

class Table extends React.Component{
    constructor(){
        super()
        this.state = {
            table: []
        }
    }

    componentDidMount(){
        const api_key = '8ae6dd9f44804cdfa79b7cd38f77d800'
        const option = {
            method: 'GET', 
            headers: { 'X-Auth-Token': `${api_key}`  }
        }
        fetch(`http://api.football-data.org/v2/competitions/PL/standings`, option)
        .then(res => res.json())
        .then(data => {
            // console.log(data.standings[0].table)
            this.setState({table: data.standings[0].table})})
            

    }
    render(){
        const newArr = this.state.table.map((item)=>{
            return(
                
                    <tr key={item.position}>
                        <td> {item.position}</td>
                        <td><img src = {item.team.crestUrl} width = '20' height = '20' alt='team logo'/> {item.team.name}</td>
                        <td>{item.playedGames}</td>
                        <td>{item.won}</td>
                        <td>{item.lost}</td>
                        <td>{item.draw}</td>
                        <td>{item.goalsFor}</td>
                        <td>{item.goalsAgainst}</td>
                        <td>{item.goalDifference}</td>
                        <td>{item.points}</td>
                    </tr>
                
            )
            
        })
       
        return(
            <div>
                <table className='table table-striped table-bordered table-hover'>
                    <thead>
                    <tr>
                        <th>Pos</th>
                        <th>Team </th>
                        <th>Played</th>
                        <th>Wins</th>
                        <th>Lost</th>
                        <th>Drawn</th>
                        <th>For</th>
                        <th>Against</th>
                        <th>Difference</th>
                        <th>Points</th>
                     </tr>
                    </thead>
                    <tbody>
                        {newArr}
                    </tbody>
                </table>
            </div>
        )
    }
}

export default Table