import React from 'react';

class Teams extends React.Component{
    constructor(){
        super()
        this.state = {
            team: []
        }
    }

    componentDidMount(){
        // console.log('bipin')
        const api_key = '8ae6dd9f44804cdfa79b7cd38f77d800'
        const option = {
            method: 'GET', 
            headers: { 'X-Auth-Token': `${api_key}`  }
        }
        fetch(`http://api.football-data.org/v2/competitions/PL/teams`, option)
        .then(res => res.json())
        .then(data => {
            this.setState({team : data.teams})
            // console.log(data)
        })


    }
    render(){
        const Arr = this.state.team
        // console.log(Arr)
        const newArr = Arr.map((item)=>{
            return(
                
                <div key={item.id} className="col-xl-3 col-lg-4 col-sm-6" style={{marginBottom: '35px'}}>
                    <div className="card">
                    <img className="card-img-top" src={item.crestUrl} alt="Cardcap" width='200' height = '200'/>
                    <div className="card-body">
                    <h5 className="card-title">{item.name}</h5>
                    <p className="card-text">{item.address}</p>
                    </div>
                    <ul className="list-group list-group-flush">
                    <li className="list-group-item">Venue: {item.venue}</li>
                    <li className="list-group-item">Founded: {item.founded}</li>
                    <li className="list-group-item">Phone: {item.phone}</li>
                    </ul>
                    <div className="card-body">
                    <a href={item.website} className="card-link">OFFICIAL SITE</a>
                    </div>
                    </div> 
                </div>
                
            )
            
        })
       
        return(
            <div className='row'>          
                {newArr}
            </div>
        )
    }
}

export default Teams