import React from 'react';
import ReactDOM from 'react-dom';
// import Header from './components/Header'
// import Main from './components/Main'
import Table from './football_comp/Table'
import Teams from './football_comp/Teams.js'
import Headers from './football_comp/Headers'
import Fixtures from './football_comp/Fixtures'
import Players from './football_comp/Players'
import Stats from './football_comp/Stats'
import Home from './football_comp/Home'

import {BrowserRouter as Router, Route, Switch}from 'react-router-dom'

// function MeowerApp(){
//     return(
//       <div>
//         <Header />
//         <Main />
       
//      </div>
//     )
// }

function FootballApp(){
  return(
    <Router>
        <div>
          <Headers/>
          <Switch>
          
          <Route path='/' exact component={Home}/>
          <Route path='/fixtures' component={Fixtures} />
          <Route path='/table' component={Table} />
          <Route path='/teams' component={Teams} />
          <Route path='/players' component={Players} />
          <Route path='/stats' component={Stats} />
          </Switch>
      </div>
    </Router>
    
  )
}

ReactDOM.render(
<FootballApp />,
  document.getElementById('root')
);